// Reusable UI components: waypoint list, action buttons, location button, navigation status.

@Composable
fun TripStatus(waypointCount: Int) {
    Div({
        style {
            padding(12.px)
            marginBottom(16.px)
            backgroundColor(Color("#f5f5f5"))
            borderRadius(8.px)
        }
    }) {
        Text("Status: $waypointCount waypoints")
    }
}

@Composable
fun WaypointList(
    waypoints: List<Waypoint>,
    onRemove: (Int) -> Unit,
    onReorder: (fromIndex: Int, toIndex: Int) -> Unit = { _, _ -> }
) {
    var draggedIndex by remember { mutableStateOf<Int?>(null) }
    var dragOverIndex by remember { mutableStateOf<Int?>(null) }

    Div({
        style {
            marginBottom(16.px)
        }
    }) {
        H3 { Text("Waypoints") }

        if (waypoints.isEmpty()) {
            P({
                style { color(Color("#666")) }
            }) {
                Text("No waypoints added yet. Search for an address below to add one.")
            }
        } else {
            waypoints.forEachIndexed { listPosition, waypoint ->
                WaypointItem(
                    waypoint = waypoint,
                    onRemove = onRemove,
                    isDragOver = dragOverIndex == listPosition,
                    onDragStart = { draggedIndex = listPosition },
                    onDragOver = { dragOverIndex = listPosition },
                    onDragLeave = { if (dragOverIndex == listPosition) dragOverIndex = null },
                    onDrop = {
                        val from = draggedIndex
                        if (from != null && from != listPosition) {
                            onReorder(from, listPosition)
                        }
                        draggedIndex = null
                        dragOverIndex = null
                    },
                    onDragEnd = {
                        draggedIndex = null
                        dragOverIndex = null
                    }
                )
            }
        }
    }
}

@Composable
fun WaypointItem(
    waypoint: Waypoint,
    onRemove: (Int) -> Unit,
    isDragOver: Boolean = false,
    onDragStart: () -> Unit = {},
    onDragOver: () -> Unit = {},
    onDragLeave: () -> Unit = {},
    onDrop: () -> Unit = {},
    onDragEnd: () -> Unit = {}
) {
    Div({
        attr("draggable", "true")
        addEventListener("dragstart") { onDragStart() }
        addEventListener("dragover") { event ->
            event.preventDefault()
            onDragOver()
        }
        addEventListener("dragleave") { onDragLeave() }
        addEventListener("drop") { event ->
            event.preventDefault()
            onDrop()
        }
        addEventListener("dragend") { onDragEnd() }
        style {
            display(DisplayStyle.Flex)
            justifyContent(JustifyContent.SpaceBetween)
            alignItems(AlignItems.Center)
            padding(12.px)
            marginBottom(8.px)
            backgroundColor(if (isDragOver) Color("#e3f2fd") else Color("#fff"))
            border(
                2.px,
                LineStyle.Solid,
                if (isDragOver) Color("#2196F3") else Color("#ddd")
            )
            borderRadius(4.px)
            cursor("grab")
            property("transition", "background-color 0.15s, border-color 0.15s")
        }
    }) {
        Span {
            Text("${waypoint.index + 1}. ${waypoint.address}")
        }
        Button({
            onClick { onRemove(waypoint.index) }
            style {
                backgroundColor(Color("#dc3545"))
                color(Color.white)
                border(0.px, LineStyle.None, Color.transparent)
                padding(6.px, 12.px)
                borderRadius(4.px)
                cursor("pointer")
            }
        }) {
            Text("X")
        }
    }
}

@Composable
fun ActionButtons(waypoints: List<Waypoint>, onClearAll: () -> Unit = {}) {
    var showConfirmDialog by remember { mutableStateOf(false) }
    val isEnabled = waypoints.isNotEmpty()

    Div({
        style {
            display(DisplayStyle.Flex)
            flexDirection(FlexDirection.Column)
            gap(8.px)
        }
    }) {
        Button({
            onClick {
                if (isEnabled) {
                    showConfirmDialog = true
                }
            }
            style {
                backgroundColor(if (isEnabled) Color("#dc3545") else Color("#ccc"))
                color(Color.white)
                border(0.px, LineStyle.None, Color.transparent)
                padding(12.px, 24.px)
                borderRadius(4.px)
                cursor(if (isEnabled) "pointer" else "not-allowed")
                fontSize(16.px)
            }
        }) {
            Text("Clear All")
        }
    }

    if (showConfirmDialog) {
        ConfirmDialog(
            message = "Are you sure you want to clear all ${waypoints.size} waypoints?",
            onConfirm = {
                showConfirmDialog = false
                onClearAll()
            },
            onCancel = {
                showConfirmDialog = false
            }
        )
    }
}

@Composable
fun ConfirmDialog(
    message: String,
    onConfirm: () -> Unit,
    onCancel: () -> Unit
) {
    // Overlay
    Div({
        style {
            property("position", "fixed")
            property("top", "0")
            property("left", "0")
            property("right", "0")
            property("bottom", "0")
            backgroundColor(Color("rgba(0, 0, 0, 0.5)"))
            display(DisplayStyle.Flex)
            justifyContent(JustifyContent.Center)
            alignItems(AlignItems.Center)
            property("z-index", "1000")
        }
        onClick { onCancel() }
    }) {
        // Dialog box
        Div({
            style {
                backgroundColor(Color.white)
                padding(24.px)
                borderRadius(8.px)
                property("max-width", "400px")
                property("box-shadow", "0 4px 20px rgba(0, 0, 0, 0.3)")
            }
            onClick { it.stopPropagation() }
        }) {
            P({
                style {
                    margin(0.px)
                    marginBottom(20.px)
                    fontSize(16.px)
                    color(Color("#333"))
                }
            }) {
                Text(message)
            }

            Div({
                style {
                    display(DisplayStyle.Flex)
                    justifyContent(JustifyContent.FlexEnd)
                    gap(12.px)
                }
            }) {
                Button({
                    onClick { onCancel() }
                    style {
                        backgroundColor(Color("#6c757d"))
                        color(Color.white)
                        border(0.px, LineStyle.None, Color.transparent)
                        padding(10.px, 20.px)
                        borderRadius(4.px)
                        cursor("pointer")
                        fontSize(14.px)
                    }
                }) {
                    Text("Cancel")
                }
                Button({
                    onClick { onConfirm() }
                    style {
                        backgroundColor(Color("#dc3545"))
                        color(Color.white)
                        border(0.px, LineStyle.None, Color.transparent)
                        padding(10.px, 20.px)
                        borderRadius(4.px)
                        cursor("pointer")
                        fontSize(14.px)
                    }
                }) {
                    Text("Clear All")
                }
            }
        }
    }
}

@Composable
fun MyLocationButton(mapController: MapController?) {
    val scope = rememberCoroutineScope()

    Button({
        onClick {
            scope.launch {
                try {
                    val currentPosition = getCurrentPosition()
                    mapController?.centerOn(currentPosition.first, currentPosition.second)
                } catch (e: Exception) {
                    Napier.e("Failed to get location: ${e.message}")
                }
            }
        }
        style {
            backgroundColor(Color("#6c757d"))
            color(Color.white)
            border(0.px, LineStyle.None, Color.transparent)
            padding(12.px, 24.px)
            borderRadius(4.px)
            cursor("pointer")
            fontSize(16.px)
            marginTop(8.px)
            width(100.percent)
        }
    }) {
        Text("Center on My Location")
    }
}

internal suspend fun getCurrentPosition(): Pair<Double, Double> =
    suspendCoroutine { cont ->
        val nav = window.navigator.asDynamic()
        if (nav.geolocation != null && nav.geolocation != undefined) {
            nav.geolocation.getCurrentPosition(
                { pos: dynamic ->
                    val lat = pos.coords.latitude as Double
                    val lng = pos.coords.longitude as Double
                    cont.resume(Pair(lat, lng))
                },
                { err: dynamic ->
                    cont.resumeWithException(Exception("Geolocation error: ${err.message}"))
                }
            )
        } else {
            cont.resumeWithException(Exception("Geolocation not supported"))
        }
    }